#block-fws-raf-mainnavigation {
  display: none;
}

@media (min-width: 992px) {
  header#navbar .navbar-header {
    min-width: 250px;
  }
}
@media (max-width: 991px) {
  header#navbar > .container {
    width: 100%;
  }
}
header#navbar #navbar-collapse {
  border-top: 0;
  box-shadow: none;
}

@media (max-width: 991px) {
  .mb-menuwrapper {
    height: calc(100% - 115px);
    top: 115px;
  }
}
.fws-branding__logo img {
  width: 100%;
}

.navbar-main {
  width: auto;
  margin-bottom: 0;
}
.navbar-main .navbar-nav {
  float: right;
  margin-top: 20px;
}
.navbar-main .navbar-nav > li > a {
  font-size: 1.062em;
  font-weight: 700;
  text-transform: uppercase;
}
.navbar-main .dropdown-menu {
  background-color: #fff;
  list-style: none;
  padding: 0;
}
.navbar-main .dropdown-menu > li > a {
  background-color: #fff;
  color: #333;
  padding: 10px 15px;
}
.navbar-main .dropdown-menu > li > a:hover,
.navbar-main .dropdown-menu > li > a:focus {
  background-color: #337ab7;
  color: #fff;
}

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  color: #fff;
}

.views-element-container .view-content .views-field a {
  white-space: nowrap;
}
.views-element-container .panel-title a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.views-element-container .panel-title a:not(.collapsed) .fa-caret-down {
  transform: rotate(180deg);
}
.views-element-container .panel-body {
  padding: 20px;
}

.views-exposed-form .form-type-select .select-wrapper select,
.views-exposed-form .form-type-select .select-wrapper select option {
  min-width: 85px;
  min-height: 36px;
}
.views-exposed-form .form-type-select .select-wrapper:after {
  top: 27px;
  right: 0;
  z-index: 10;
}
.views-exposed-form .form-item {
  margin-right: 20px;
  margin-bottom: 10px;
}
.views-exposed-form .form-item label {
  margin-right: 10px;
}
.views-exposed-form .form-item .form-text {
  max-width: 150px;
}
.views-exposed-form .form-actions {
  margin-bottom: 10px;
}